  // ç¹å»äºä»¶ï¼æ¯å¦äºæ¥
  showitem(item: { title: any; visible: boolean }): void {
    this.outer.emit(item);
    if (this.mutex === true) {
      this.config.layers.forEach((value: { title: any; visible: boolean }) => {
        if (value.title === item.title) {
          value.visible = true;
        } else {
          value.visible = false;
        }
      });
    } else {
      item.visible = !item.visible;
    }
    this.sum = 0;
    this.config.layers.forEach((value: { title: any; visible: boolean }) => {
      if (value.visible === true) {
        this.sum++;
        if (this.sum === this.config.layers.length) {
          this.flag = true;
        } else {
          this.flag = false;
        }
      }
    });
  }
  // å¨é
  allitem(): void {
    this.flag = true;
    this.config.layers.forEach((value: { title: any; visible: boolean }) => {
      value.visible = true;
    });
  }
  // å¨ä¸é
  cancelitem(): void {
    this.flag = false;
    this.config.layers.forEach((value: { title: any; visible: boolean }) => {
      value.visible = false;
    });
  }

